body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #000;
  color: #fff;
  overflow-x: hidden; /* Verhindert horizontales Scrollen */
  overflow-y: scroll;
}

.app {
  position: relative;
  width: 100%;
  height: 100%;
}

.hamburger-menu {
  position: fixed;
  top: 20px;
  left: 20px;
  font-size: 24px;
  background: transparent;
  color: #fff;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  z-index: 10;
  transition: transform 0.2s ease;
}

.hamburger-menu:hover {
  transform: scale(1.1);
}

.menu {
  position: fixed;
  top: 0;
  left: -250px; /* Start außerhalb des Bildschirms */
  width: 250px;
  height: 100%;
  background: #333333af;
  backdrop-filter: blur(10px);
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
  padding-top: 50px;
  transition: left 0.3s ease; /* Slide-in-Effekt */
  z-index: 9;
}

.menu.show {
  left: 0; /* Menü ins Bild schieben */
}

.menu nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu nav ul li {
  margin: 20px 0;
}

.menu nav ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  padding: 10px;
  display: block;
}

.menu nav ul li a:hover {
  background: #555;
  border-radius: 5px;
}

.content {
  position: relative;
  margin-left: 0;
  transition: margin-left 0.3s ease;

}

.menu.show ~ .content {
  margin-left: 0px; /* Platz schaffen, wenn Menü offen ist */
}
