.game-container {
  position: relative;
  width: 100%;
  height: 100vh;
  background: linear-gradient(to bottom, #87ceeb, #f9d342);
  overflow: hidden;
}

.monkey {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 2rem;

}

.banana, .coconut {
  position: absolute;
  font-size: 2rem;
  top: 0;
}

.score {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.5rem;
  font-weight: bold;
  color:#f9d342;
}

.game-over {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.bananaButton {
  background: #f9d342; /* Gelb */
  border: none;
  border-radius: 50px; /* Runde Ecken */
  color: #fff;
  padding: 15px 30px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 
    8px 8px 16px rgba(0, 0, 0, 0.2), 
    -8px -8px 16px rgba(255, 255, 255, 0.5);
  transition: all 0.3s ease;
}

.bananaButton:hover {
  box-shadow: 
    inset 8px 8px 16px rgba(0, 0, 0, 0.2), 
    inset -8px -8px 16px rgba(255, 255, 255, 0.5);
}

.bananaButton:active {
  transform: scale(0.95); /* Leichte Größenänderung beim Klicken */
}

.control-buttons {
  position: fixed;
  bottom: 20px; /* Buttons am unteren Bildschirmrand */
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 20px; /* Abstand zwischen den Buttons */
}

.left-button,
.right-button {
  background-color: #f4c542; /* Gelb wie eine Banane */
  border: none;
  border-radius: 10px;
  padding: 15px 25px;
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.left-button:hover,
.right-button:hover {
  background-color: #e6b938; /* Etwas dunkler beim Hover */
}

.left-button:active,
.right-button:active {
  transform: scale(0.95); /* Leichtes Schrumpfen beim Klicken */
}
