  color: greenyellow;
}

/* Stil für den Canvas */
.banana-canvas {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

/* Overlay für den Text */
.contact-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  z-index: 2;
  background: rgba(0, 0, 0, 0.6); /* Halbtransparentes Schwarz */
  color: white;
  padding: 20px;
  border-radius: 10px;
  backdrop-filter: blur(5px); /* Blur-Effekt */
}

/* E-Mail-Link */
.email-link {
  color: #ffeb3b; /* Gelbe Farbe */
  font-size: 18px;
  text-decoration: none;
  font-weight: bold;
}

.email-link:hover {
  text-decoration: underline;
}

/* Responsives Design */
@media screen and (max-width: 768px) {
  .contact-overlay {
    padding: 15px;
    font-size: 14px;
  }
}
