h1{
    margin: 20px;
    margin-top: 8rem;
}

p{
 margin: 20px;
 line-height: 1.2rem;
 letter-spacing: 0.3rem;

}

.about{
    position: relative;
    display: block;
    width: 80%;
    left: 10%;
    margin-top: 4rem;
}
.about1{
    display: flex;
    background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  padding: 20px 40px;
  border-radius: 15px;
}

.about-img{
    height: 300px;
    border-radius: 6px;
}
@media screen and (max-width: 1200px) {
    .about1 {
      flex-direction: column;
    }
  }
  