.service-page {
    
    font-family: Arial, sans-serif;
  }
  
  .service-content {
    max-width: 900px;
    margin: 0 auto;
    background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  padding: 20px 40px;
  border-radius: 15px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .service1 {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .service-img {
    width: 100%;
    height: auto;
    border-radius: 10px;
  }
  
  
  
  ul {
    margin: 20px 0;
    padding-left: 40px;
    list-style-type: disc;
  }
  
  ul li {
    margin: 10px 0;
  }
  
  
  