body {
  margin: 0;
  overflow: hidden;
  font-family: Arial, sans-serif;
  background-image: url('bananas1.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  z-index: 1000;
}

.hamburger {
  background: none;
  color: white;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.menu {
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu-item a {
  color: white;
  text-decoration: none;
  padding: 10px;
  font-size: 18px;
}

/* Für Webkit-Browser (Chrome, Edge, Safari) */
::-webkit-scrollbar {
  width: 4px; /* Breite des Scrollbalkens */
}

::-webkit-scrollbar-track {
  background: #fcf4c4; /* Gelber Hintergrund wie eine Banane */
  border-radius: 6px; /* Abgerundete Ecken */
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2); /* Leichter Schatten */
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #f4c542, #d4a721); /* Farbverlauf wie eine Banane */
  border-radius: 6px; /* Abgerundete Ecken */
  border: 2px solid #ffe135; /* Rahmen in Bananenfarbe */
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #e6b938, #c69b1e); /* Dunklere Farbe beim Hover */
}

/* Für Firefox - Styling durch Scroll-Container */
.scroll-container {
  scrollbar-width: thin; /* Dünner Scrollbalken */
  scrollbar-color: #f4c542 #ffe135; /* Thumb- und Track-Farben */
  overflow-y: scroll; /* Aktiviert Scrollen */
}

.scroll-container::-webkit-scrollbar-track {
  background: #fcf4c4;
  border-radius: 6px;
}

.scroll-container::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #f4c542, #d4a721);
  border-radius: 6px;
}

/* Nur für das Styling des Containers, falls der Scrollbalken versteckt sein sollte */
.scroll-container {
  height: 100vh;
  width: 100%;
  overflow-y: auto;
  margin: 0;
}
