body {
  margin: 0;
  overflow: hidden;
  font-family: Arial, sans-serif;
}

.home-container {
  position: relative;
  width: 100%;
  height: 100vh;
  background: black;
}

.banana-canvas {
  width: 100%;
  height: 100%;
  display: block;
}

.overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  padding: 20px 40px;
  border-radius: 15px;
  color: white;
  text-align: center;
  font-size: 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.6);
  z-index: 2;
  
}

.overlay h1 {
  margin: 0;
  font-size: 2.5rem;
  font-weight: bold;
  text-shadow: 0 2px 5px rgba(0, 0, 0, 0.8);
}

.overlay p {
  font-size: 1rem;
}
